/**
 * RetirementRouteContainer.js
 * The wrapper container for retirement
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { change, reset } from 'redux-form';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';

import { resetCompare } from '../../actions/compareActions';
import { getPromoGiveaways, resetInsuranceForm, FORMSTATES } from '../../actions/insuranceActions';
import { clearRetirementData } from '../../actions/retirementActions';
import { isProduction } from '../../lib/tagFunctions';

import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

import RetirementMainContainer from './RetirementMainContainer';
import RetirementChoosePlanContainer from './RetirementChoosePlanContainer';
import RetirementDetailContainer from './RetirementDetailContainer';

import { routeHandler } from '../../lib/helperFunctions';

const formName = 'retirement';
const detailFormName = 'retirement-detail';

class RetirementRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(formName, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    const {
      resetStoreForm,
      retirementPromoGiveAway,
      clearData,
      history,
    } = this.props;

    // history.replace(`/retirement${history.location.search}`);

    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('retirement');
    resetStoreForm('retirement-detail');

    if (retirementPromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderLifeForms = this.renderLifeForms.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.addEventListener('beforeunload', this.handleLeavePage);
    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('retirement');
    resetStoreForm('retirement-detail');
  }

  setInitialFormState() {
    const { changeForm } = this.props;

    changeForm(formName, 'birth_date', '01 Jan 1990');
    changeForm(formName, 'retirement_age', '60');
    changeForm(formName, 'premium_term', '15 years');
    changeForm(formName, 'payout_term', '15 years');

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      const surname = userDetails && userDetails.surname ? userDetails.surname : '';
      const firstname = userDetails && userDetails.firstName ? ` ${userDetails.firstName}` : '';

      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'name', `${surname}${firstname}`);
      changeForm(detailFormName, 'birth_date', userDetails.dateOfBirth);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'Choose Plan',
          'Details',
        ]}
        form="life"
        stateList={[
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
        ]}
        stateDescriptions={[
          { main: '' /* 'Choose your Plan', sub: 'Compare price plans from multiple companies' */},
          { main: 'Fill in your details - Get $5 Credits' },
        ]}
        charged={false}
      />
    );
  }

  renderLifeForms() {
    const { formIndex } = this.state;
    const {
      userDetails,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);


    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return <RetirementChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <RetirementDetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      default:
        return <RetirementMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const { retirementPromoGiveAway, constants } = this.props;

    if (!constants || retirementPromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'PolicyPal, retirement insurance, singapore retirement insurance, retirement planning, retirement, CPF, annuity, financial planning, pension, 401k, retirement calculator, retiree, retirement accounts, retirement quotes, best retirement plans, retirement fund, insured retirement plan, retirement planning singapore, 401k plan, early retirement, pension plan' : '';
    return (
      <Page id="retirement">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Retirement Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Retirement Insurance" />
          <meta name="description" content="Browse products for retirement insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {this.renderStepProgress()}
        {this.renderLifeForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Page>
    );
  }
}

RetirementRouteContainer.defaultProps = {
  userDetails: null,
  retirementPromoGiveAway: undefined,
  constants: null,
};

RetirementRouteContainer.propTypes = {
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  getPromoGiveaways: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  userDetails: PropTypes.object,
  retirementPromoGiveAway: PropTypes.object,
  constants: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  retirementPromoGiveAway: state.insurance.promoGiveaways.retirement,
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  userDetails: state.userDetails.data,
});

const mapDispatchToProps = dispatch => ({
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('retirement'));
  },
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearRetirementData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RetirementRouteContainer));
